import {http} from './../http'

export function transactionAll(offset, limit) {
    let data = {
        offset: offset ?? "",
        limit: limit ?? 10,
        status: ["Active", "Pending"]
    }

    return http.post('transactions/all', data)
}

export function activeTransaction(transaction) {
    let data = {
        code: transaction.code,
        money: transaction.value
    }

    return http.post('transactions/active', data)
}

export function searchTransaction(code) {
    let data = {
        code: code
    }

    return http.post('transactions/all', data)
}