<template>
    <div class="container-fluid p-0">
        <h1 class="h3 mb-3">Danh sách giao dịch</h1>
        <div class="row">
            <div class="col-12 col-xl-12">
                <div class="card ">
                    <div class="demo-input-suffix" style="margin: 15px">
                        <div style="margin-top: 15px;">
                            <el-input placeholder="Mã giao dịch" v-model="transactionCode" class="input-with-select col-lg-12 search-user">
                                <el-button slot="append" icon="el-icon-search" @click="searchTransaction"></el-button>
                            </el-input>
                        </div>
                    </div>
                    <div class="table-responsive">
                        <table class="table">
                            <thead>
                                <tr>
                                    <th>STT</th>
                                    <th>Mã giao dịch</th>
                                    <th>Người tạo</th>
                                    <th>Giá trị</th>
                                    <th>Bonus</th>
                                    <th>Thời gian</th>
                                    <th>Trạng thái</th>
                                    <th>Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(transition, index) in listTransaction" :key="index">
                                    <td>{{index + 1}}</td>
                                    <td>{{transition.code}}</td>
                                    <td>{{transition.fullname + ' - ' + transition.username}}</td>
                                    <td>{{new Intl.NumberFormat().format(transition.value)}}đ</td>
                                    <td>{{new Intl.NumberFormat().format(transition.bonus)}}đ</td>
                                    <td>{{formatDate(transition.createdAt)}}</td>
                                    <td><span class="badge" :class="transition.status == 'Active' ? 'bg-success' : 'bg-warning'">
                                        {{transition.status == 'Active' ? 'Hoàn thành' : 'Chờ thanh toán'}}
                                    </span></td>
                                    <td class="table-action" >
                                        <el-button type="primary" size="small" v-if="transition.status == 'Pending'" :loading="loading" @click="activeTransaction(transition)">Duyệt</el-button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <scroll-loader 
                            :loader-method="transactions" 
                            :loader-size="20" 
                            :loader-disable="completedLoadmore">
                        </scroll-loader>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { transactionAll, activeTransaction, searchTransaction} from './../../api/transactions'
    import moment from 'moment'
    export default {
        name: "Transactions",
        metaInfo: {
            title: 'Quản lý giao dịch',
        },
        data() {
            return {
                offset: '',
                limit: 20,
                listTransaction: [],
                completedLoadmore: false,
                dialogVisible: false,
                phoneNumber:'',
                loading: false,
                transactionCode: ''
            }
        },
        methods: {
            async transactions() {
                try {
                    this.$store.state.auth.loading = true
                    let listTransaction = await transactionAll(this.offset, this.limit)
                    this.$store.state.auth.loading = false
                    if (listTransaction.data.total < this.limit) {
                        this.completedLoadmore = true;
                    }
                    if (listTransaction.data.data) {
                        listTransaction.data.data.forEach(transaction => {
                            this.listTransaction.push(transaction)
                        });
                    }
                    this.offset = listTransaction.data.last_offset
                    
                } catch (error) {
                    console.log(error)
                    this.$store.state.auth.loading = false
                    this.completedLoadmore = true;
                    this.$notify.error({
                        title: 'Error',
                        message: error.response.data.message,
                    });
                }
            },
            formatDate(date) {
                const format = "HH:mm:ss DD-MM-YYYY"
                var dateTime = moment(date).format(format);
                return dateTime
            },
            async activeTransaction(transaction) {
                try {
                    this.loading = true
                    await activeTransaction(transaction)
                    this.updateStatusById(transaction)
                    this.dialogVisible = false
                    this.loading = false
                    this.$notify({
                        title: 'Thành công',
                        message: "Duyệt thành công",
                        type: 'success'
                    });
                } catch (error) {
                    console.log(error)
                    this.loading = false
                    this.completedLoadmore = true;
                    this.$notify.error({
                        title: 'Error',
                        message: error.response.data.message,
                    });
                }
            },
            updateStatusById(transaction) {
                for (var i=0; i < this.listTransaction.length; i++) {
                    if (this.listTransaction[i].id == transaction.id) {
                        this.listTransaction[i].status = "Active"
                    }
                }
            },
            async searchTransaction() {
                try {
                    if (this.transactionCode) {
                        this.completedLoadmore = true
                        this.listTransaction = []
                        this.$store.state.auth.loading = true
                        let transaction = await searchTransaction(this.transactionCode)
                        this.$store.state.auth.loading = false
                        if  (transaction.data.total == 0) {
                            this.$notify.error({
                                title: 'Error',
                                message: "Không tìm thấy giao dịch",
                            });
                        } else {
                            transaction.data.data.forEach(transaction => {
                                this.listTransaction.push(transaction)
                            });
                        }
                    } else {
                        this.offset = ''
                        this.completedLoadmore = false
                        this.listTransaction = []
                    }
                } catch (error) {
                    this.$store.state.auth.loading = false
                    this.$notify.error({
                        title: 'Error',
                        message: error.response.data.message,
                    });
                }
            }
        },
        mounted() {
            
        }
    }
</script>