<template>
    <div class="container-fluid p-0">
        <h1 class="h3 mb-3">Danh sách gói nạp tiền</h1>
        <div class="row">
            <div class="col-12 col-xl-12">
                <div class="card ">
                    <div class="table-responsive">
                        <table class="table">
                            <thead>
                                <tr>
                                    <th>STT</th>
                                    <th>Số tiền</th>
                                    <th>Khuyến mại</th>
                                    <th>Trạng thái</th>
                                    <th>Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(packages, index) in listPackages" :key="index">
                                    <td>{{index + 1}}</td>
                                    <td>{{new Intl.NumberFormat().format(packages.money)}}đ</td>
                                    <td>{{new Intl.NumberFormat().format(packages.bonus)}}%</td>
                                    <td><span class="badge" :class="packages.status == 'Active' ? 'bg-success' : 'bg-warning'">
                                        {{packages.status == 'Active' ? 'Hoạt động' : 'Tạm dừng'}}
                                    </span></td>
                                    <td class="table-action">
                                        <el-button size="mini" >Edit</el-button>
                                        <el-button size="mini" type="danger" @click="dialogVisible = true">Delete</el-button>
                                    </td>
                                    <el-dialog
                                        title="Thông báo"
                                        :visible.sync="dialogVisible"
                                        width="30%">
                                        <span>Bạn có chắc muốn xóa?</span>
                                        <span slot="footer" class="dialog-footer">
                                            <el-button @click="dialogVisible = false">Hủy</el-button>
                                            <el-button type="primary" >Đồng ý</el-button>
                                        </span>
                                    </el-dialog>
                                </tr>
                            </tbody>
                        </table>
                        <scroll-loader 
                            :loader-method="packages" 
                            :loader-size="20" 
                            :loader-disable="completedLoadmore">
                        </scroll-loader>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { packagesAll } from './../../api/packages'
    export default {
        name: "ListUser",
        metaInfo: {
            title: 'Quản lý gói nạp tiền',
        },
        data() {
            return {
                offset: '',
                limit: 20,
                listPackages: [],
                completedLoadmore: false,
                dialogVisible: false,
            }
        },
        methods: {
            async packages() {
                try {
                    this.$store.state.auth.loading = true
                    let listPackages = await packagesAll()
                    this.$store.state.auth.loading = false
                    if (listPackages.data.data.total < this.limit) {
                        this.completedLoadmore = true;
                    }
                    console.log(listPackages)
                    if (listPackages.data.data.data) {
                        listPackages.data.data.data.forEach(packages => {
                            this.listPackages.push(packages)
                        });
                    }
                    this.offset = listPackages.data.last_offset
                    
                } catch (error) {
                    console.log(error)
                    this.$store.state.auth.loading = false
                    this.completedLoadmore = true;
                    this.$notify.error({
                        title: 'Error',
                        message: error.response.data.message,
                    });
                }
            },
        }
    }
</script>